@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&family=Roboto:wght@400;700;900&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Roboto Mono', monospace;
}


.App {
  text-align: center;
  font-family: 'Roboto', sans-serif;
  padding: 20px;

  @media print {
    padding: 0px;
  }
}

html {
  @media print {
    font-size: 12px;
  }
}

a {
  color: black;
}

body {
  zoom: 95%;
}

